import React, {useContext, useEffect, useState } from 'react'
import { v4 } from 'uuid';
import { NotificationContext } from '../Notifications/NotificationProvider';
import { useDrop, useDrag } from 'react-dnd'
import ak from './ak.jpg'
import characterPic from './character.png'
import clock from './clock.png'

const Card = ({ character, gameContract, blurBackground, playGame, unstakeWeapon, stakeWeapon }) => {

    const dispatch = useContext(NotificationContext)

    const [characterInfo, setCharacterInfo] = useState(null)
    const [{ isOver } , drop] = useDrop(() => ({
        accept: "weapon",
        drop : (item) => attemptStake(character, item.weapon),//stakeWeapon(character, weapon)
        collect: (monitor) => ({
            isOver: !!monitor.isOver()
          })
    }))
    const [{ isDragging } , drag] = useDrag(() => ({
        type: "characterWeapon",
        item : { 
            character: character
        },
        collect: (monitor) => ({
          isDragging: !!monitor.isDragging()
        })
        
      }))

    useEffect(() => {
        getCharacterInfo()
        setInterval(() => {
            getCharacterInfo()
          }, 10000)

    }, [])

    async function getCharacterInfo() {
        let characterArray = await gameContract.getFullCardInfo(character)
        if (characterArray !== characterInfo){
            setCharacterInfo(characterArray)
        }
    }

    async function attemptStake(character, weapon) {
        await gameContract.getCharacterWeapon(character) == 0 ? stakeWeapon(character, weapon) : 
        dispatch({
            type: "ADD_NOTIFICATION",
            payload: {
                id: v4(),
                type: "ERROR",
                message: "Character Already Has a Weapon",
                link : ""
            }
        })
    }

    async function attemptUnstake(character){
        console.log(character)
        await gameContract.getCharacterWeapon(character) !== 0 ? unstakeWeapon(character) :
        dispatch({
            type: "ADD_NOTIFICATION",
            payload: {
                id: v4(),
                type: "ERROR",
                message: "Character Already Has a Weapon",
                link : ""
            }
        })
    }
    
    function getAge(ageInSeconds) {
        let seconds = Number(ageInSeconds)
        let days = Math.floor(seconds / (3600 * 24))
        let hours = Math.floor((seconds - (days * (3600 * 24))) / 3600)
        let minutes = Math.floor((seconds - (days * (3600 * 24)) - (hours * 3600)) / 60)
        let sec = seconds - (days * (3600 * 24)) - (hours * 3600) - (minutes * 60)
        return days + "d " + hours + "h " + minutes + "min " + sec + "sec"
    }

    function getUrl(ipfs){
        if (ipfs.includes("ipfs://blank")){
            return "https://nftchecker.mypinata.cloud/ipfs/Qmb3B7fQQ7Wh24G8EntvYuVLxttUnTMkB1XCPwd42fzdfY"
        }else{
            let ipfsUrl = ipfs.replace("ipfs://" , "")
            //return "https://nftchecker.mypinata.cloud/ipfs/QmRiZ2vkBSUCsSpG94jVuz7rk5xiKizdMbdHs9mEiDBeSG/AR1.png"
            return "https://gateway.pinata.cloud/ipfs/" + ipfsUrl
        }
    }

  return (
    // <div className={backgroundBlur ? 'blurScreen' : 'none'}>
    <div>
     {/* {
         frontCenter &&
         <div className='cardFrontCenterOuter'> </div>
     }    */}
        <div  /*className ={mainView ? "frontCenterCard" : "card"} onClick={frontAndCenter}*/ >
            { characterInfo &&
            <div className='card'  ref={drop}>
                <h3> Character {character} </h3>
                <p> <img className='clock' alt="" src={clock}></img> {getAge(characterInfo[7])} </p>
                <img src={characterPic /*getUrl(characterInfo[0])*/} alt="" className="character"/>
                <div className= "slots">
                    <div className='slot'>
                        <img className={`slotPicture ${characterInfo[9]}`} alt="" ref={drag} onClick={() => attemptUnstake(character)} src={getUrl(characterInfo[1])} />
                        <div>
                            {characterInfo[2] !== "No Weapon Staked" && " + " + characterInfo[2]}
                            {characterInfo[2] !== "No Weapon Staked" && <br></br>}
                            {characterInfo[2] !== "No Weapon Staked" ? " Reward " : "No Weapon" }
                        </div>
                    </div>
                    <div className='slot'>
                    <img className='slotPicture' alt="" src={getUrl(characterInfo[3])} />
                        <div>
                            No Accessory
                            {/* {characterInfo[4]} */}
                        </div>
                    </div>
                    <div className='slot'>
                    <img className='slotPicture' alt="" src={getUrl(characterInfo[5])} />
                        <div>
                            No
                            <br></br>
                            Perk
                            {/* {characterInfo[6]} */}
                        </div>
                    </div>
                </div>
                {   
                    (Number(characterInfo[8]) + (24 * 60 * 60)  <= (new Date().getTime() / 1000))  ?
                    <button className="btn" onClick={() => playGame(character)}> Collect Bounty </button> :
                    characterInfo[8] === 0 ?
                    <div>
                    <button className="btn" onClick={() => playGame(character)}> Collect Bounty </button>
                    </div> :
                    <div>
                        <p> Next Bounty <br></br> <img className='clock' alt="" src={clock}></img>{getAge((Number(characterInfo[8]) + (24 * 60 * 60)) - Math.round((new Date().getTime() / 1000)))} </p>
                    </div>
                }
            </div>
            }
        </div>
    </div>

   )
}

export default Card