import React, { useEffect, useState } from 'react'
import {useDrag, useDrop} from 'react-dnd'

const Weapon = ({ weapon, gameContract, weaponContract, selectWeapon, selectedWeapons }) => {

    const [weaponUri, setWeaponUri] = useState(null);
    const [selected, setSelected] = useState(false);
    const [rarity, setRarity] = useState(null)
    const [{ isDragging } , drag] = useDrag(() => ({
      type: "weapon",
      item : { weapon: weapon},
      collect: (monitor) => ({
        isDragging: !!monitor.isDragging()
      })
    }))
    const [{ isOver } , drop] = useDrop(() => ({
      accept: "characterWeapon",
      drop : (item) => attemptUpgrade(item.weapon, weapon),//stakeWeapon(character, weapon)
      collect: (monitor) => ({
          isOver: !!monitor.isOver()
        })
  }))

  async function attemptUpgrade(weapon1, weapon2){
    console.log(weapon1)
    console.log(weapon2)
  }

  async function getWeaponInfo () {
    let weaponRarity = await weaponContract.getRarity(weapon)
    if (weaponRarity != rarity){
      setRarity(weaponRarity)
    }
    let uri = await weaponContract.getWeaponUri(weapon)
    let ipfsUrl = uri.replace("ipfs://" , "")
    let completeUri = "https://gateway.pinata.cloud/ipfs/" + ipfsUrl
    //let completeUri = "https://nftchecker.mypinata.cloud/ipfs/QmRiZ2vkBSUCsSpG94jVuz7rk5xiKizdMbdHs9mEiDBeSG/AR1.png"
    if (completeUri != weaponUri){
      setWeaponUri(completeUri)
    }

  }

  function select() {
    if(selectedWeapons.length <= 1 || selected){
        setSelected(!selected)
        selectWeapon(weapon, !selected)
    }
  }

    

    useEffect( () => {
      getWeaponInfo()
      setInterval( () => {
        getWeaponInfo()
      }, 10000)
    })


  return (
    <div >
          { <img ref={drag} src={weaponUri} onClick={select} className={`weapon ${selected && rarity }`}></img> }
    </div>
  )
}

export default Weapon