import React , {useContext, useEffect, useState }from 'react'
import { v4 } from 'uuid';
import { NotificationContext } from '../Notifications/NotificationProvider.js';
import Card from './Card.js'


const Cards = ({ gameContract, characters , mysteryCharacter, blurBackground, mintCharacter, playGame, stakeWeapon, unstakeWeapon, getCharacters}) => {

    const dispatch = useContext(NotificationContext)

    

    useEffect(() => {
    }, [gameContract])


  return (
    <div className='cards'>
        <h3> Your Characters <button className="btn" onClick={getCharacters}> Refresh </button>  <button className="btn" onClick={mysteryCharacter}> Mint </button></h3>
        <p>  </p>
        <div className = "characters">
            { characters && characters.map((character, index) => (
                <Card key={index} character={character} blurBackground={blurBackground} playGame={playGame} stakeWeapon={stakeWeapon} unstakeWeapon={unstakeWeapon} gameContract={gameContract}/>
                ))}
        </div>
        
    </div>
  )
}

export default Cards