import React, { useContext, useState, useEffect }from 'react'
import { v4 } from 'uuid';
import { NotificationContext } from '../Notifications/NotificationProvider.js';
import Weapon from './Weapon.js'
import {useDrop} from 'react-dnd'

const Weapons = ({ gameContract, weaponContract, getWeapons, mysteryWeapon, upgradeWeapon, unstakeWeapon, mintWeapon, weapons }) => {

    const dispatch = useContext(NotificationContext)

    const [selectedWeapons, setSelectedWeapons] = useState([])

    const [{ isOver } , drop] = useDrop(() => ({
        accept: "characterWeapon",
        drop : (item) => attemptUnstake(item.character),//stakeWeapon(character, weapon)
        collect: (monitor) => ({
            isOver: !!monitor.isOver()
          })
    }))

    useEffect(() => {
    })

    async function attemptUnstake(character){
        let characterWeapon = await gameContract.getCharacterWeapon(character)
        characterWeapon != 0 ? unstakeWeapon(character) :
        dispatch({
            type: "ADD_NOTIFICATION",
            payload: {
                id: v4(),
                type: "ERROR",
                message: "Character Has No Weapon to Unstake",
                link : ""
            }
        })
    }

    function selectWeapon(weaponToSelect, bool){
        console.log(weaponToSelect)
        console.log(bool)
        if (bool == true){
            let tempSelectedWeapons = [...selectedWeapons, weaponToSelect]
            setSelectedWeapons(tempSelectedWeapons)
        }else{
            let tempSelectedWeapons = selectedWeapons.filter(weapon => weapon != weaponToSelect)
            setSelectedWeapons(tempSelectedWeapons)
        }
    }

    async function prepareUpgradeWeapons(){
        if (selectedWeapons.length < 2){
            dispatch({
                type: "ADD_NOTIFICATION",
                payload: {
                    id: v4(),
                    type: "ERROR",
                    message: "Must Choose 2 Weapons",
                    link : ""
                }
            })
            return
        }
        let rarity1 = await weaponContract.getRarity(selectedWeapons[0])
        let rarity2 = await weaponContract.getRarity(selectedWeapons[1])
        if (rarity1 != rarity2){
            dispatch({
                type: "ADD_NOTIFICATION",
                payload: {
                    id: v4(),
                    type: "ERROR",
                    message: "2 Weapons Must Be Same Rarity",
                    link : ""
                }
            })
            return
        }else{
            setSelectedWeapons([])
            upgradeWeapon(selectedWeapons[0], selectedWeapons[1])
            
        }

    }

    

    
  return (
        <div className="weapons" ref={drop}>
            <h3> Your Weapons <button className="btn" onClick={getWeapons}> Refresh </button> <button className="btn" onClick={mysteryWeapon}> Mint </button> <button className='btn' onClick={prepareUpgradeWeapons}> Upgrade </button></h3>
            <p>  </p>
            <div className='weaponContainer'>
                { weapons && weapons.map(( weapon, index ) => (
                    <Weapon  className= 'weapons' key={index} selectedWeapons={selectedWeapons} selectWeapon={selectWeapon} weapon={weapon} gameContract={gameContract} weaponContract = {weaponContract}/>
                ))}
            </div>
        </div>
  )
}

export default Weapons